/* ============================================
   USERSPACE - PREMIUM AI EXPERIENCE CENTER
   ============================================ */

/* ===== ROOT VARIABLES ===== */
:root {
    /* Primary Colors */
    --primary-gradient: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    --secondary-gradient: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
    --success-gradient: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
    --warning-gradient: linear-gradient(135deg, #fa709a 0%, #fee140 100%);
    
    /* AI Theme Colors */
    --ai-purple: #8b5cf6;
    --ai-blue: #3b82f6;
    --ai-cyan: #06b6d4;
    --ai-pink: #ec4899;
    --ai-orange: #f59e0b;
    --ai-green: #10b981;
    
    /* Glassmorphism */
    --glass-bg: rgba(255, 255, 255, 0.05);
    --glass-border: rgba(255, 255, 255, 0.1);
    --glass-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.15);
    
    /* Spacing */
    --spacing-xs: 0.5rem;
    --spacing-sm: 1rem;
    --spacing-md: 1.5rem;
    --spacing-lg: 2rem;
    --spacing-xl: 3rem;
    
    /* Border Radius */
    --radius-sm: 8px;
    --radius-md: 12px;
    --radius-lg: 16px;
    --radius-xl: 24px;
    --radius-full: 9999px;
    
    /* Transitions */
    --transition-fast: 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-base: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-slow: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-bounce: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    
    /* Effects */
    --glow-sm: 0 0 10px rgba(139, 92, 246, 0.3);
    --glow-md: 0 0 20px rgba(139, 92, 246, 0.4);
    --glow-lg: 0 0 30px rgba(139, 92, 246, 0.5);
    
    /* Z-index */
    --z-dropdown: 1000;
    --z-sticky: 1020;
    --z-fixed: 1030;
    --z-modal: 1050;
    --z-tooltip: 1070;



    /* Dynamic Backgrounds */
    --bg-main: linear-gradient(135deg, #0a0a0f 0%, #1a1a2e 50%, #16213e 100%);
    --bg-card: rgba(255, 255, 255, 0.05);
    --bg-input: rgba(255, 255, 255, 0.05);
    
    /* Dynamic Text */
    --text-main: #ffffff;
    --text-secondary: rgba(255, 255, 255, 0.7);
    --text-muted: rgba(255, 255, 255, 0.5);
    
    /* Dynamic Borders */
    --border-color: rgba(255, 255, 255, 0.1);  
  }
  [data-theme="light"] {
    /* Light Mode Overrides */
    --bg-main: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
    --bg-card: rgba(255, 255, 255, 0.65); /* Frosty White */
    --bg-input: rgba(0, 0, 0, 0.05);
    
    --text-main: #1a1a2e; /* Dark Blue Text */
    --text-secondary: rgba(0, 0, 0, 0.7);
    --text-muted: rgba(0, 0, 0, 0.5);
    
    --border-color: rgba(0, 0, 0, 0.1);
    --glass-border: rgba(0, 0, 0, 0.1);
    --glass-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.08);
  }
  /* ===== CONTAINER ===== */
  .userspace-container {
    min-height: 100vh;
    padding: 0;
    
    background: var(--bg-main);
    color: var(--text-main);
    position: relative;
    overflow-x: hidden;
    transition: background 0.3s ease, color 0.3s ease;
  }
  
  /* Animated Background Pattern */
  .userspace-container::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: 
      radial-gradient(circle at 20% 50%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 80% 80%, rgba(59, 130, 246, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 40% 20%, rgba(236, 72, 153, 0.1) 0%, transparent 50%);
    animation: backgroundPulse 15s ease-in-out infinite;
    pointer-events: none;
    z-index: 0;
  }
  
  @keyframes backgroundPulse {
    0%, 100% { opacity: 0.5; transform: scale(1); }
    50% { opacity: 0.8; transform: scale(1.05); }
  }
  
  /* ===== FIRESTORE ERROR BANNER ===== */
  .firestore-error-banner {
    position: fixed;
    top: 70px;
    left: 50%;
    transform: translateX(-50%);
    background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
    color: var(--text-main);
    padding: 0.75rem 1.5rem;
    border-radius: var(--radius-full);
    display: flex;
    align-items: center;
    gap: 0.75rem;
    box-shadow: 0 10px 40px rgba(239, 68, 68, 0.3);
    z-index: var(--z-fixed);
    font-size: 0.875rem;
    font-weight: 500;
    animation: slideDown 0.5s var(--transition-bounce);
    backdrop-filter: blur(10px);
  }
  
  @keyframes slideDown {
    from {
      transform: translateX(-50%) translateY(-100px);
      opacity: 0;
    }
    to {
      transform: translateX(-50%) translateY(0);
      opacity: 1;
    }
  }
  
  /* ===== HERO SECTION ===== */
  .userspace-hero {
    position: relative;
    padding: 4rem 2rem 3rem;
    text-align: center;
    overflow: hidden;
    margin-bottom: 2rem;
    z-index: 1;
  }
  
  .userspace-hero.enhanced {
    background: linear-gradient(
      135deg,
      rgba(139, 92, 246, 0.1) 0%,
      rgba(59, 130, 246, 0.1) 50%,
      rgba(236, 72, 153, 0.1) 100%
    );
    backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--glass-border);
  }
  
  /* Hero Particles */
  .hero-particles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 0;
  }
  
  .particle {
    position: absolute;
    width: 4px;
    height: 4px;
    background: var(--ai-purple);
    border-radius: 50%;
    animation: particleFloat 5s infinite;
    box-shadow: 0 0 10px var(--ai-purple);
  }
  
  @keyframes particleFloat {
    0% {
      transform: translateY(100vh) scale(0);
      opacity: 0;
    }
    10% {
      opacity: 1;
    }
    90% {
      opacity: 1;
    }
    100% {
      transform: translateY(-100px) scale(1);
      opacity: 0;
    }
  }
  
  /* Hero Content */
  .hero-content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
  }
  
  .user-avatar-large {
    position: relative;
    width: 120px;
    height: 120px;
    margin: 0 auto;
    animation: heroAvatarEntry 1s var(--transition-bounce);
  }
  
  @keyframes heroAvatarEntry {
    0% {
      transform: scale(0) rotate(-180deg);
      opacity: 0;
    }
    100% {
      transform: scale(1) rotate(0deg);
      opacity: 1;
    }
  }
  
  .user-avatar-large img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    border: 4px solid var(--ai-purple);
    box-shadow: 
      0 0 0 8px rgba(139, 92, 246, 0.2),
      0 20px 60px rgba(139, 92, 246, 0.4);
    transition: all var(--transition-base);
  }
  
  .user-avatar-large:hover img {
    transform: scale(1.05) rotate(5deg);
    box-shadow: 
      0 0 0 8px rgba(139, 92, 246, 0.3),
      0 25px 70px rgba(139, 92, 246, 0.6);
  }
  
  .avatar-status {
    position: absolute;
    bottom: 8px;
    right: 8px;
    width: 20px;
    height: 20px;
    background: var(--ai-green);
    border: 3px solid #0a0a0f;
    border-radius: 50%;
    animation: statusPulse 2s ease-in-out infinite;
  }
  
  .avatar-status.online {
    background: var(--ai-green);
    box-shadow: 0 0 20px var(--ai-green);
  }
  
  @keyframes statusPulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.1); }
  }
  
  .avatar-glow {
    position: absolute;
    inset: -20px;
    background: radial-gradient(circle, rgba(139, 92, 246, 0.4) 0%, transparent 70%);
    border-radius: 50%;
    animation: glowPulse 3s ease-in-out infinite;
    pointer-events: none;
  }
  
  @keyframes glowPulse {
    0%, 100% { opacity: 0.5; transform: scale(1); }
    50% { opacity: 1; transform: scale(1.1); }
  }
  
  /* User Info Hero */
  .user-info-hero h1 {
    font-size: 2.5rem;
    font-weight: 800;
    background: linear-gradient(135deg, #b3aaaa 0%, #a28de2 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    margin: 0;
    animation: textShimmer 3s ease-in-out infinite;
    letter-spacing: -0.5px;
  }
  
  @keyframes textShimmer {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.8; }
  }
  
  .wave {
    display: inline-block;
    animation: wave 2s ease-in-out infinite;
    transform-origin: 70% 70%;
    margin-left: 0.5rem;
  }
  
  @keyframes wave {
    0%, 100% { transform: rotate(0deg); }
    10%, 30% { transform: rotate(14deg); }
    20% { transform: rotate(-8deg); }
    40%, 100% { transform: rotate(0deg); }
  }
  
  .user-info-hero p {
    font-size: 1.125rem;
    color: var(--text-secondary);
    margin: 0.5rem 0;
  }
  
  .highlight {
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-weight: 700;
  }
  
  .hero-subtitle {
    font-size: 0.875rem;
    color: rgba(255, 255, 255, 0.5);
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  
  /* RAG Status */
  .rag-status {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 0.75rem 1.5rem;
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-full);
    backdrop-filter: blur(10px);
    margin-top: 1rem;
    animation: fadeInUp 0.6s var(--transition-base) 0.3s both;
  }
  
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .status-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #6b7280;
    position: relative;
  }
  
  .status-dot.online {
    background: var(--ai-green);
    animation: statusBlink 2s ease-in-out infinite;
  }
  
  .status-dot.online::after {
    content: '';
    position: absolute;
    inset: -4px;
    border-radius: 50%;
    border: 2px solid var(--ai-green);
    animation: statusRipple 2s ease-out infinite;
  }
  
  @keyframes statusBlink {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.6; }
  }
  
  @keyframes statusRipple {
    0% {
      transform: scale(1);
      opacity: 1;
    }
    100% {
      transform: scale(2);
      opacity: 0;
    }
  }
  
  .status-text {
    color: rgba(255, 255, 255, 0.9);
    font-weight: 600;
    font-size: 0.875rem;
  }
  
  .status-stats {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.75rem;
    margin-left: auto;
  }
  
  /* ===== TAB NAVIGATION ===== */
  .userspace-tabs {
    display: flex;
    gap: 1rem;
    padding: 0 2rem 2rem;
    overflow-x: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--ai-purple) transparent;
    position: relative;
    z-index: 1;
  }
  
  .userspace-tabs::-webkit-scrollbar {
    height: 6px;
  }
  
  .userspace-tabs::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 3px;
  }
  
  .userspace-tabs::-webkit-scrollbar-thumb {
    background: var(--ai-purple);
    border-radius: 3px;
  }
  
  .tab-btn {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 1rem 1.5rem;
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    color: var(--text-secondary);
    font-weight: 600;
    cursor: pointer;
    transition: all var(--transition-base);
    backdrop-filter: blur(10px);
    position: relative;
    overflow: hidden;
  }
  
  .tab-btn::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    opacity: 0;
    transition: opacity var(--transition-base);
    z-index: 0;
  }
  
  .tab-btn > * {
    position: relative;
    z-index: 1;
  }
  
  .tab-btn:hover {
    transform: translateY(-2px);
    border-color: rgba(139, 92, 246, 0.5);
    box-shadow: 0 10px 30px rgba(139, 92, 246, 0.2);
  }
  
  .tab-btn:hover::before {
    opacity: 0.1;
  }
  
  .tab-btn.active {
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    border-color: transparent;
    color: var(--text-main);
    box-shadow: 
      0 10px 40px rgba(139, 92, 246, 0.4),
      0 0 20px rgba(139, 92, 246, 0.3);
    transform: translateY(-4px);
  }
  
  .tab-btn.active::before {
    opacity: 1;
  }
  
  .tab-icon {
    font-size: 1.5rem;
    filter: drop-shadow(0 0 8px currentColor);
  }
  
  .tab-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .tab-label {
    font-size: 0.95rem;
    font-weight: 700;
  }
  
  .tab-desc {
    font-size: 0.75rem;
    opacity: 0.8;
    font-weight: 400;
  }
  
  /* ===== CONTENT AREA ===== */
  .userspace-content {
    padding: 0 2rem 3rem;
    position: relative;
    z-index: 1;
  }
  
  .tab-content {
    animation: tabContentEntry 0.5s var(--transition-base);
  }
  
  @keyframes tabContentEntry {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* ===== SECTION HEADERS ===== */
  .section-header {
    text-align: center;
    margin-bottom: 2.5rem;
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  .section-header h2 {
    font-size: 2rem;
    font-weight: 800;
    color: var(--text-main);
    margin: 0 0 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
    flex-wrap: wrap;
  }
  
  .section-header p {
    color: rgba(255, 255, 255, 0.6);
    font-size: 1.05rem;
    margin: 0;
  }
  
  .badge {
    display: inline-flex;
    align-items: center;
    padding: 0.35rem 0.85rem;
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    color: var(--text-main);
    border-radius: var(--radius-full);
    font-size: 0.75rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
    animation: badgePulse 2s ease-in-out infinite;
  }
  
  @keyframes badgePulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.05); }
  }
  
  /* ===== AI BRAIN TAB ===== */
  .brain-layout {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 2rem;
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  @media (max-width: 1024px) {
    .brain-layout {
      grid-template-columns: 1fr;
    }
  }
  
  /* Knowledge Graph */
  .knowledge-graph-section {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 1.5rem;
    backdrop-filter: blur(20px);
    box-shadow: var(--glass-shadow);
    transition: all var(--transition-base);
  }
  
  .knowledge-graph-section:hover {
    border-color: rgba(139, 92, 246, 0.3);
    box-shadow: 0 12px 40px rgba(139, 92, 246, 0.15);
  }
  
  .section-mini-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }
  
  .section-mini-header h3 {
    color: var(--text-main);
    font-size: 1.1rem;
    font-weight: 700;
    margin: 0;
  }
  
  .node-count {
    background: rgba(139, 92, 246, 0.2);
    color: var(--ai-purple);
    padding: 0.25rem 0.75rem;
    border-radius: var(--radius-full);
    font-size: 0.75rem;
    font-weight: 600;
  }
  
  .knowledge-graph-canvas {
    width: 100%;
    height: 300px;
    border-radius: var(--radius-md);
    background: rgba(0, 0, 0, 0.2);
    cursor: crosshair;
    transition: all var(--transition-base);
  }
  
  .knowledge-graph-canvas:hover {
    background: rgba(0, 0, 0, 0.3);
  }
  
  /* Brain Container */
  .brain-container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  /* Brain Suggestions */
  .brain-suggestions {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    padding: 1.5rem;
    backdrop-filter: blur(20px);
  }
  
  .brain-suggestions p {
    color: rgba(255, 255, 255, 0.6);
    font-size: 0.875rem;
    font-weight: 600;
    margin: 0 0 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .suggestions-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 0.75rem;
  }
  
  .suggestion-chip {
    background: rgba(139, 92, 246, 0.1);
    border: 1px solid rgba(139, 92, 246, 0.3);
    color: rgba(255, 255, 255, 0.8);
    padding: 0.75rem 1rem;
    border-radius: var(--radius-md);
    font-size: 0.85rem;
    cursor: pointer;
    transition: all var(--transition-base);
    text-align: left;
    line-height: 1.4;
  }
  
  .suggestion-chip:hover {
    background: rgba(139, 92, 246, 0.2);
    border-color: var(--ai-purple);
    transform: translateY(-2px);
    box-shadow: 0 8px 20px rgba(139, 92, 246, 0.2);
    color: var(--text-main);
  }
  
  /* Retrieval Toggle */
  .retrieval-toggle {
    display: flex;
    justify-content: flex-end;
    padding: 0.5rem 0;
  }
  
  .retrieval-toggle label {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: var(--text-secondary);
    font-size: 0.875rem;
    cursor: pointer;
    user-select: none;
    transition: color var(--transition-fast);
  }
  
  .retrieval-toggle label:hover {
    color: var(--text-main);
  }
  
  .retrieval-toggle input[type="checkbox"] {
    width: 18px;
    height: 18px;
    cursor: pointer;
    accent-color: var(--ai-purple);
  }
  
  /* Brain Chat */
  .brain-chat {
    background: var(--bg-card);
    border: 1px solid var(--border-color);
    border-radius: var(--radius-xl);
    backdrop-filter: blur(20px);
    overflow: hidden;
    box-shadow: var(--glass-shadow);
    display: flex;
    flex-direction: column;
    height: 600px;
  }
  
  .chat-messages {
    flex: 1;
    overflow-y: auto;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    scrollbar-width: thin;
    scrollbar-color: var(--ai-purple) transparent;
  }
  
  .chat-messages::-webkit-scrollbar {
    width: 6px;
  }
  
  .chat-messages::-webkit-scrollbar-track {
    background: transparent;
  }
  
  .chat-messages::-webkit-scrollbar-thumb {
    background: var(--ai-purple);
    border-radius: 3px;
  }
  
  /* Chat Welcome */
  .chat-welcome {
    text-align: center;
    padding: 3rem 2rem;
    animation: fadeIn 0.8s var(--transition-base);
  }
  
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  .welcome-icon {
    font-size: 4rem;
    display: block;
    margin-bottom: 1rem;
    animation: float 3s ease-in-out infinite;
  }
  
  @keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }
  
  .chat-welcome h3 {
    color: var(--text-main);
    font-size: 1.5rem;
    font-weight: 700;
    margin: 0 0 0.5rem;
  }
  
  .chat-welcome p {
    color: rgba(255, 255, 255, 0.6);
    margin: 0 0 1.5rem;
    line-height: 1.6;
  }
  
  .rag-features {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
  }
  
  .rag-features span {
    background: rgba(139, 92, 246, 0.15);
    border: 1px solid rgba(139, 92, 246, 0.3);
    padding: 0.5rem 1rem;
    border-radius: var(--radius-full);
    font-size: 0.85rem;
    color: rgba(255, 255, 255, 0.8);
  }
  
  /* Chat Messages */
  .chat-message {
    display: flex;
    gap: 1rem;
    animation: messageSlideIn 0.4s var(--transition-base);
    max-width: 85%;
  }
  
  @keyframes messageSlideIn {
    from {
      opacity: 0;
      transform: translateX(-20px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
  .chat-message.user {
    margin-left: auto;
    flex-direction: row-reverse;
    animation: messageSlideIn 0.4s var(--transition-base);
  }
  
  @keyframes messageSlideIn {
    from { opacity: 0; transform: translateX(20px); }
    to { opacity: 1; transform: translateX(0); }
  }
  
  .message-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.25rem;
    flex-shrink: 0;
    box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
  }
  
  .chat-message.user .message-avatar {
    background: linear-gradient(135deg, var(--ai-blue), var(--ai-cyan));
  }
  
  .message-content {
    background: rgba(139, 92, 246, 0.1);
    border: 1px solid rgba(139, 92, 246, 0.2);
    padding: 1rem 1.25rem;
    border-radius: var(--radius-lg);
    color: rgba(255, 255, 255, 0.9);
    line-height: 1.6;
    position: relative;
    overflow: hidden;
  }
  
  .chat-message.user .message-content {
    background: rgba(59, 130, 246, 0.1);
    border-color: rgba(59, 130, 246, 0.2);
  }
  
  .message-content.loading {
    padding: 1.5rem;
  }
  
  /* Retrieved Docs */
  .retrieved-docs {
    margin-top: 1.5rem;
    padding-top: 1.5rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    animation: fadeInUp 0.5s var(--transition-base);
  }
  
  .retrieved-docs h4 {
    color: var(--text-secondary);
    font-size: 0.875rem;
    font-weight: 600;
    margin: 0 0 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .retrieved-doc {
    background: rgba(0, 0, 0, 0.2);
    padding: 0.75rem;
    border-radius: var(--radius-sm);
    margin-bottom: 0.75rem;
    border-left: 3px solid var(--ai-cyan);
  }
  
  .retrieved-doc:last-child {
    margin-bottom: 0;
  }
  
  .doc-score {
    display: inline-block;
    background: var(--ai-cyan);
    color: #0a0a0f;
    padding: 0.2rem 0.5rem;
    border-radius: var(--radius-sm);
    font-size: 0.7rem;
    font-weight: 700;
    margin-right: 0.5rem;
  }
  
  .doc-type {
    display: inline-block;
    background: rgba(255, 255, 255, 0.1);
    color: var(--text-secondary);
    padding: 0.2rem 0.5rem;
    border-radius: var(--radius-sm);
    font-size: 0.7rem;
    font-weight: 600;
  }
  
  .doc-content {
    margin: 0.5rem 0 0;
    font-size: 0.85rem;
    color: rgba(255, 255, 255, 0.6);
    line-height: 1.5;
  }
  
  /* RAG Loading */
  .rag-loading {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .loading-step {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
    font-size: 0.875rem;
    opacity: 0.5;
    transition: all var(--transition-base);
  }
  
  .loading-step.active {
    color: var(--ai-cyan);
    opacity: 1;
    font-weight: 600;
  }
  
  .loading-step.active::before {
    content: '';
    animation: loadingDot 1s ease-in-out infinite;
  }
  
  @keyframes loadingDot {
    0%, 100% { opacity: 0.3; }
    50% { opacity: 1; }
  }
  
  /* Chat Input */
  .chat-input-container {
    display: flex;
    gap: 1rem;
    padding: 1.5rem;
    background: rgba(0, 0, 0, 0.2);
    border-top: 1px solid var(--glass-border);
    backdrop-filter: blur(10px);
  }
  
  .chat-input-container input {
    flex: 1;
    background: var(--bg-input);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    padding: 1rem 1.25rem;
    color: var(--text-main);
    font-size: 0.95rem;
    transition: all var(--transition-base);
  }
  
  .chat-input-container input:focus {
    outline: none;
    border-color: var(--ai-purple);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0 0 0 4px rgba(139, 92, 246, 0.1);
  }
  
  .chat-input-container input::placeholder {
    color: rgba(255, 255, 255, 0.4);
  }
  
  .send-btn {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    border: none;
    color: var(--text-main);
    font-size: 1.5rem;
    cursor: pointer;
    transition: all var(--transition-base);
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 15px rgba(139, 92, 246, 0.4);
  }
  
  .send-btn:hover:not(:disabled) {
    transform: scale(1.1) rotate(15deg);
    box-shadow: 0 6px 25px rgba(139, 92, 246, 0.6);
  }
  
  .send-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  /* ===== MULTI-AGENT TAB ===== */
  .agents-container {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  /* Agent Selector */
  .agent-selector {
    background: var(--bg-card);
    border: 1px solid var(--border-color);
    border-radius: var(--radius-xl);
    padding: 2rem;
    backdrop-filter: blur(20px);
  }
  
  .agent-selector h3 {
    color: var(--text-main);
    font-size: 1.25rem;
    font-weight: 700;
    margin: 0 0 1.5rem;
  }
  
  .agent-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 1rem;
  }
  
  .agent-select-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.75rem;
    padding: 1.5rem 1rem;
    background: rgba(255, 255, 255, 0.03);
    border: 2px solid transparent;
    border-radius: var(--radius-lg);
    cursor: pointer;
    transition: all var(--transition-base);
    position: relative;
    overflow: hidden;
  }
  
  .agent-select-btn::before {
    content: '';
    position: absolute;
    inset: 0;
    background: currentColor;
    opacity: 0;
    transition: opacity var(--transition-base);
  }
  
  .agent-select-btn:hover {
    transform: translateY(-4px);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  }
  
  .agent-select-btn.selected {
    transform: translateY(-4px);
    box-shadow: 0 10px 40px currentColor;
  }
  
  .agent-select-btn.selected::before {
    opacity: 0.1;
  }
  
  .agent-icon {
    font-size: 2.5rem;
    filter: drop-shadow(0 4px 8px currentColor);
  }
  
  .agent-name {
    color: var(--text-main);
    font-weight: 700;
    font-size: 0.95rem;
    text-align: center;
  }
  
  .agent-expertise {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.75rem;
    text-align: center;
    line-height: 1.4;
  }
  
  /* Active Agents Bar */
  .active-agents-bar {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    padding: 1.5rem;
    background: rgba(0, 0, 0, 0.2);
    border-radius: var(--radius-lg);
    animation: slideDown 0.5s var(--transition-bounce);
  }
  
  .agent-status-chip {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.75rem 1.25rem;
    background: rgba(255, 255, 255, 0.05);
    border: 2px solid currentColor;
    border-radius: var(--radius-full);
    transition: all var(--transition-base);
  }
  
  .agent-status-chip.thinking {
    animation: chipPulse 1.5s ease-in-out infinite;
  }
  
  @keyframes chipPulse {
    0%, 100% { opacity: 1; transform: scale(1); }
    50% { opacity: 0.7; transform: scale(0.98); }
  }
  
  .agent-status-chip.complete {
    background: rgba(16, 185, 129, 0.1);
  }
  
  .agent-chip-icon {
    font-size: 1.25rem;
  }
  
  .agent-chip-name {
    color: var(--text-main);
    font-weight: 600;
    font-size: 0.875rem;
  }
  
  .agent-chip-status {
    font-size: 1rem;
    margin-left: 0.25rem;
  }
  
  /* Agent Task Input */
  .agent-task-input {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 2rem;
    backdrop-filter: blur(20px);
  }
  
  .agent-task-input h3 {
    color: var(--text-main);
    font-size: 1.25rem;
    font-weight: 700;
    margin: 0 0 1rem;
  }
  
  .agent-task-input textarea {
    width: 100%;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    padding: 1.25rem;
    color: var(--text-main);
    font-size: 0.95rem;
    font-family: inherit;
    resize: vertical;
    transition: all var(--transition-base);
  }
  
  .agent-task-input textarea:focus {
    outline: none;
    border-color: var(--ai-purple);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0 0 0 4px rgba(139, 92, 246, 0.1);
  }
  
  .agent-task-input textarea::placeholder {
    color: rgba(255, 255, 255, 0.3);
    line-height: 1.6;
  }
  
  .run-agents-btn {
    margin-top: 1.5rem;
    width: 100%;
    padding: 1.25rem 2rem;
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    border: none;
    border-radius: var(--radius-lg);
    color: var(--text-main);
    font-size: 1.05rem;
    font-weight: 700;
    cursor: pointer;
    transition: all var(--transition-base);
    box-shadow: 0 10px 30px rgba(139, 92, 246, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
  }
  
  .run-agents-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 15px 40px rgba(139, 92, 246, 0.5);
  }
  
  .run-agents-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  .btn-spinner {
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top-color: var(--text-main);
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
  }
  
  @keyframes spin {
    to { transform: rotate(360deg); }
  }
  
  /* Agent Conversation */
  .agent-conversation {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 2rem;
    backdrop-filter: blur(20px);
    max-height: 700px;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--ai-purple) transparent;
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  .agent-conversation::-webkit-scrollbar {
    width: 6px;
  }
  
  .agent-conversation::-webkit-scrollbar-thumb {
    background: var(--ai-purple);
    border-radius: 3px;
  }
  
  .agent-message {
    margin-bottom: 2rem;
    animation: messageSlideIn 0.5s var(--transition-base);
    border-left: 3px solid currentColor;
    padding-left: 1.5rem;
  }
  
  .agent-message.orchestrator {
    border-left-color: var(--ai-cyan);
  }
  
  .agent-message.agent-response {
    border-left-color: inherit;
  }
  
  .agent-message.error {
    border-left-color: #ef4444;
  }
  
  .message-header {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    margin-bottom: 0.75rem;
  }
  
  .msg-icon {
    font-size: 1.5rem;
  }
  
  .msg-sender {
    color: var(--text-main);
    font-weight: 700;
    font-size: 1rem;
  }
  
  .msg-status {
    margin-left: auto;
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.85rem;
    font-style: italic;
  }
  
  .message-body {
    color: rgba(255, 255, 255, 0.9);
    line-height: 1.7;
    font-size: 0.95rem;
    white-space: pre-wrap;
  }
  
  .message-body.final {
    background: rgba(139, 92, 246, 0.1);
    padding: 1.5rem;
    border-radius: var(--radius-md);
    border: 1px solid rgba(139, 92, 246, 0.3);
    margin-top: 0.5rem;
  }
  
  /* Agents Placeholder */
  .agents-placeholder {
    text-align: center;
    padding: 4rem 2rem;
    animation: fadeIn 0.8s var(--transition-base);
  }
  
  .agents-animation {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-bottom: 2rem;
  }
  
  .floating-agent {
    font-size: 3rem;
    animation: float 3s ease-in-out infinite;
    filter: drop-shadow(0 8px 16px rgba(139, 92, 246, 0.4));
  }
  
  .agents-placeholder h3 {
    color: var(--text-main);
    font-size: 1.75rem;
    font-weight: 700;
    margin: 0 0 0.75rem;
  }
  
  .agents-placeholder p {
    color: rgba(255, 255, 255, 0.6);
    margin: 0 0 2rem;
    font-size: 1.05rem;
  }
  
  .agent-capabilities {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
  }
  
  .agent-capabilities span {
    background: rgba(139, 92, 246, 0.1);
    border: 1px solid rgba(139, 92, 246, 0.3);
    padding: 0.5rem 1rem;
    border-radius: var(--radius-full);
    font-size: 0.875rem;
    color: rgba(255, 255, 255, 0.8);
  }
  
  /* ===== SKILL MATCH TAB ===== */
  .skills-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  @media (max-width: 1024px) {
    .skills-container {
      grid-template-columns: 1fr;
    }
  }
  
  /* Skills Input Section */
  .skills-input-section {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 2rem;
    backdrop-filter: blur(20px);
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .skills-input-section textarea {
    width: 100%;
    height: 100%;
    min-height: 400px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    padding: 1.25rem;
    color: var(--text-main);
    font-size: 0.95rem;
    font-family: 'Courier New', monospace;
    resize: none;
    transition: all var(--transition-base);
  }
  
  .skills-input-section textarea:focus {
    outline: none;
    border-color: var(--ai-purple);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0 0 0 4px rgba(139, 92, 246, 0.1);
  }
  
  .skills-input-section textarea::placeholder {
    color: rgba(255, 255, 255, 0.3);
    line-height: 1.6;
  }
  
  .analyze-btn {
    padding: 1.25rem 2rem;
    background: linear-gradient(135deg, var(--ai-blue), var(--ai-cyan));
    border: none;
    border-radius: var(--radius-lg);
    color: var(--text-main);
    font-size: 1.05rem;
    font-weight: 700;
    cursor: pointer;
    transition: all var(--transition-base);
    box-shadow: 0 10px 30px rgba(59, 130, 246, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
  }
  
  .analyze-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 15px 40px rgba(59, 130, 246, 0.5);
  }
  
  .analyze-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  /* Skills Analysis */
  .skills-analysis {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 2rem;
    backdrop-filter: blur(20px);
    animation: fadeInScale 0.6s var(--transition-bounce);
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  
  @keyframes fadeInScale {
    from {
      opacity: 0;
      transform: scale(0.9);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  /* Match Score Card */
  .match-score-card {
    display: flex;
    align-items: center;
    gap: 2rem;
    padding: 2rem;
    background: rgba(0, 0, 0, 0.2);
    border-radius: var(--radius-lg);
    border: 1px solid var(--glass-border);
  }
  
  .score-circle {
    position: relative;
    width: 140px;
    height: 140px;
    border-radius: 50%;
    background: conic-gradient(
      var(--ai-green) calc(var(--score) * 1%),
      rgba(255, 255, 255, 0.1) 0
    );
    display: flex;
    align-items: center;
    justify-content: center;
    animation: scoreReveal 1.5s var(--transition-base);
    flex-shrink: 0;
  }
  
  @keyframes scoreReveal {
    from {
      transform: rotate(-90deg);
      opacity: 0;
    }
    to {
      transform: rotate(0);
      opacity: 1;
    }
  }
  
  .score-circle::before {
    content: '';
    position: absolute;
    inset: 10px;
    background: #0a0a0f;
    border-radius: 50%;
    z-index: 1;
  }
  
  .score-value {
    position: relative;
    z-index: 2;
    font-size: 2.5rem;
    font-weight: 800;
    background: linear-gradient(135deg, var(--ai-green), var(--ai-cyan));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .score-info {
    flex: 1;
  }
  
  .score-info h3 {
    color: var(--text-main);
    font-size: 1.75rem;
    font-weight: 800;
    margin: 0 0 0.5rem;
    background: linear-gradient(135deg, var(--ai-green), var(--ai-cyan));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .score-info p {
    color: var(--text-secondary);
    line-height: 1.6;
    margin: 0;
  }
  
  /* Analysis Grid */
  .analysis-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1.5rem;
  }
  
  .analysis-card {
    background: rgba(0, 0, 0, 0.2);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    padding: 1.5rem;
    transition: all var(--transition-base);
  }
  
  .analysis-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    border-color: rgba(139, 92, 246, 0.3);
  }
  
  .analysis-card h4 {
    color: var(--text-main);
    font-size: 1.1rem;
    font-weight: 700;
    margin: 0 0 1.25rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .skill-tags {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .skill-tag {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-md);
    padding: 0.75rem;
    transition: all var(--transition-base);
  }
  
  .skill-tag:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: rgba(139, 92, 246, 0.3);
  }
  
  .skill-tag.high {
    border-left: 3px solid var(--ai-green);
  }
  
  .skill-tag.medium {
    border-left: 3px solid var(--ai-orange);
  }
  
  .skill-tag.low {
    border-left: 3px solid var(--ai-pink);
  }
  
  .skill-name {
    display: block;
    color: var(--text-main);
    font-weight: 600;
    margin-bottom: 0.25rem;
  }
  
  .skill-evidence {
    display: block;
    color: rgba(255, 255, 255, 0.6);
    font-size: 0.85rem;
  }
  
  /* Relevant Project */
  .relevant-project {
    background: rgba(139, 92, 246, 0.1);
    border: 1px solid rgba(139, 92, 246, 0.3);
    border-radius: var(--radius-md);
    padding: 1rem;
    margin-bottom: 1rem;
  }
  
  .relevant-project:last-child {
    margin-bottom: 0;
  }
  
  .relevant-project strong {
    display: block;
    color: var(--text-main);
    font-weight: 700;
    margin-bottom: 0.5rem;
  }
  
  .relevant-project p {
    color: var(--text-secondary);
    margin: 0 0 0.5rem;
    font-size: 0.9rem;
  }
  
  .highlight-text {
    display: inline-block;
    background: var(--ai-cyan);
    color: var(--text-main);
    padding: 0.25rem 0.6rem;
    border-radius: var(--radius-sm);
    font-size: 0.75rem;
    font-weight: 600;
  }
  
  /* Tips List */
  .analysis-card.tips ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .analysis-card.tips li {
    background: rgba(255, 255, 255, 0.05);
    padding: 0.75rem 1rem;
    border-radius: var(--radius-md);
    margin-bottom: 0.75rem;
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.9rem;
    line-height: 1.5;
    border-left: 3px solid var(--ai-cyan);
    transition: all var(--transition-fast);
  }
  
  .analysis-card.tips li:hover {
    background: rgba(255, 255, 255, 0.08);
    transform: translateX(4px);
  }
  
  /* ===== MOCK INTERVIEW TAB ===== */
  .interview-setup {
    max-width: 800px;
    margin: 0 auto;
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  .interview-roles {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 2.5rem;
    backdrop-filter: blur(20px);
  }
  
  .interview-roles h3 {
    color: var(--text-main);
    font-size: 1.5rem;
    font-weight: 700;
    margin: 0 0 1.5rem;
    text-align: center;
  }
  
  .role-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
    margin-bottom: 2rem;
  }
  
  .role-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.75rem;
    padding: 1.5rem 1rem;
    background: rgba(255, 255, 255, 0.05);
    border: 2px solid transparent;
    border-radius: var(--radius-lg);
    cursor: pointer;
    transition: all var(--transition-base);
    position: relative;
    overflow: hidden;
  }
  
  .role-card::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    opacity: 0;
    transition: opacity var(--transition-base);
  }
  
  .role-card > * {
    position: relative;
    z-index: 1;
  }
  
  .role-card:hover {
    transform: translateY(-4px);
    border-color: rgba(139, 92, 246, 0.5);
    box-shadow: 0 10px 30px rgba(139, 92, 246, 0.2);
  }
  
  .role-card:hover::before {
    opacity: 0.1;
  }
  
  .role-card.selected {
    border-color: var(--ai-purple);
    box-shadow: 0 10px 40px rgba(139, 92, 246, 0.4);
    transform: translateY(-4px);
  }
  
  .role-card.selected::before {
    opacity: 0.15;
  }
  
  .role-icon {
    font-size: 2.5rem;
    filter: drop-shadow(0 4px 8px rgba(139, 92, 246, 0.3));
  }
  
  .role-label {
    color: var(--text-main);
    font-weight: 700;
    font-size: 0.95rem;
    text-align: center;
  }
  
  /* Custom Role Input */
  .custom-role {
    margin-bottom: 2rem;
  }
  
  .custom-role input {
    width: 100%;
    padding: 1.25rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    color: var(--text-main);
    font-size: 0.95rem;
    transition: all var(--transition-base);
  }
  
  .custom-role input:focus {
    outline: none;
    border-color: var(--ai-purple);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0 0 0 4px rgba(139, 92, 246, 0.1);
  }
  
  .custom-role input::placeholder {
    color: rgba(255, 255, 255, 0.4);
  }
  
  .start-interview-btn {
    width: 100%;
    padding: 1.5rem 2rem;
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    border: none;
    border-radius: var(--radius-lg);
    color: var(--text-main);
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    transition: all var(--transition-base);
    box-shadow: 0 10px 30px rgba(139, 92, 246, 0.3);
  }
  
  .start-interview-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 15px 40px rgba(139, 92, 246, 0.5);
  }
  
  .start-interview-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  /* Interview Session */
  .interview-session {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    backdrop-filter: blur(20px);
    overflow: hidden;
    animation: fadeInScale 0.6s var(--transition-bounce);
  }
  
  .interview-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 2rem;
    background: rgba(0, 0, 0, 0.3);
    border-bottom: 1px solid var(--glass-border);
  }
  
  .interview-role-badge {
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    color: var(--text-main);
    padding: 0.5rem 1.25rem;
    border-radius: var(--radius-full);
    font-weight: 600;
    font-size: 0.9rem;
  }
  
  .end-interview-btn {
    padding: 0.5rem 1.25rem;
    background: rgba(239, 68, 68, 0.2);
    border: 1px solid rgba(239, 68, 68, 0.5);
    border-radius: var(--radius-full);
    color: #ef4444;
    font-weight: 600;
    font-size: 0.85rem;
    cursor: pointer;
    transition: all var(--transition-base);
  }
  
  .end-interview-btn:hover {
    background: rgba(239, 68, 68, 0.3);
    border-color: #ef4444;
    transform: scale(1.05);
  }
  
  /* Interview Chat */
  .interview-chat {
    height: 500px;
    overflow-y: auto;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    scrollbar-width: thin;
    scrollbar-color: var(--ai-purple) transparent;
  }
  
  .interview-message {
    display: flex;
    gap: 1rem;
    animation: messageSlideIn 0.5s var(--transition-base);
  }
  
  .interview-message.candidate {
    flex-direction: row-reverse;
  }
  
  .interview-message .message-avatar {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--ai-orange), var(--ai-pink));
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    flex-shrink: 0;
    box-shadow: 0 4px 15px rgba(245, 158, 11, 0.3);
  }
  
  .interview-message.candidate .message-avatar {
    background: linear-gradient(135deg, var(--ai-blue), var(--ai-cyan));
  }
  
  .message-bubble {
    max-width: 75%;
    background: rgba(245, 158, 11, 0.1);
    border: 1px solid rgba(245, 158, 11, 0.3);
    border-radius: var(--radius-lg);
    padding: 1.25rem;
  }
  
  .interview-message.candidate .message-bubble {
    background: rgba(59, 130, 246, 0.1);
    border-color: rgba(59, 130, 246, 0.3);
  }
  
  .message-role {
    display: block;
    font-weight: 700;
    font-size: 0.85rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    margin-bottom: 0.5rem;
    color: var(--text-secondary);
  }
  
  .message-bubble p {
    color: rgba(255, 255, 255, 0.9);
    line-height: 1.7;
    margin: 0;
    white-space: pre-wrap;
  }
  
  .message-bubble.loading {
    display: flex;
    gap: 0.5rem;
    align-items: center;
    justify-content: center;
    padding: 1.5rem;
  }
  
  .typing-dot {
    width: 8px;
    height: 8px;
    background: white;
    border-radius: 50%;
    animation: typingDot 1.4s ease-in-out infinite;
  }
  
  .typing-dot:nth-child(2) {
    animation-delay: 0.2s;
  }
  
  .typing-dot:nth-child(3) {
    animation-delay: 0.4s;
  }
  
  @keyframes typingDot {
    0%, 60%, 100% {
      transform: translateY(0);
      opacity: 0.7;
    }
    30% {
      transform: translateY(-10px);
      opacity: 1;
    }
  }
  
  /* Interview Input */
  .interview-input-container {
    padding: 1.5rem 2rem;
    background: rgba(0, 0, 0, 0.3);
    border-top: 1px solid var(--glass-border);
    display: flex;
    gap: 1rem;
    align-items: flex-end;
  }
  
  .interview-input-container textarea {
    flex: 1;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    padding: 1rem 1.25rem;
    color: var(--text-main);
    font-size: 0.95rem;
    font-family: inherit;
    resize: none;
    transition: all var(--transition-base);
  }
  
  .interview-input-container textarea:focus {
    outline: none;
    border-color: var(--ai-purple);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0 0 0 4px rgba(139, 92, 246, 0.1);
  }
  
  .interview-input-container textarea::placeholder {
    color: rgba(255, 255, 255, 0.4);
  }
  
  .send-response-btn {
    padding: 1rem 2rem;
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    border: none;
    border-radius: var(--radius-lg);
    color: var(--text-main);
    font-weight: 700;
    cursor: pointer;
    transition: all var(--transition-base);
    box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
  }
  
  .send-response-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(139, 92, 246, 0.5);
  }
  
  .send-response-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  /* ===== AI LAB TAB ===== */
  .lab-experiments {
    display: grid;
    grid-template-columns: 280px 1fr;
    gap: 2rem;
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  @media (max-width: 1024px) {
    .lab-experiments {
      grid-template-columns: 1fr;
    }
  }
  
  /* Experiment Selector */
  .experiment-selector {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 1.5rem;
    backdrop-filter: blur(20px);
    height: fit-content;
    position: sticky;
    top: 2rem;
  }
  
  .experiment-card {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.03);
    border: 2px solid transparent;
    border-radius: var(--radius-lg);
    cursor: pointer;
    transition: all var(--transition-base);
    margin-bottom: 0.75rem;
  }
  
  .experiment-card:hover {
    background: rgba(255, 255, 255, 0.06);
    transform: translateX(4px);
  }
  
  .experiment-card.selected {
    background: rgba(139, 92, 246, 0.15);
    border-color: var(--ai-purple);
    box-shadow: 0 4px 20px rgba(139, 92, 246, 0.2);
  }
  
  .exp-icon {
    font-size: 1.75rem;
    filter: drop-shadow(0 2px 4px rgba(139, 92, 246, 0.3));
  }
  
  .exp-info {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
  }
  
  .exp-name {
    color: var(--text-main);
    font-weight: 700;
    font-size: 0.9rem;
  }
  
  .exp-desc {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.75rem;
  }
  
  /* Experiment Workspace */
  .experiment-workspace {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  
  @media (max-width: 768px) {
    .experiment-workspace {
      grid-template-columns: 1fr;
    }
  }
  
  .exp-input,
  .exp-output {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .exp-input label,
  .exp-output label {
    color: var(--text-main);
    font-weight: 700;
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .exp-input textarea {
    flex: 1;
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    padding: 1.25rem;
    color: var(--text-main);
    font-size: 0.95rem;
    font-family: 'Courier New', monospace;
    resize: none;
    backdrop-filter: blur(20px);
    transition: all var(--transition-base);
  }
  
  .exp-input textarea:focus {
    outline: none;
    border-color: var(--ai-purple);
    box-shadow: 0 0 0 4px rgba(139, 92, 246, 0.1);
  }
  
  .exp-input textarea::placeholder {
    color: rgba(255, 255, 255, 0.3);
  }
  
  .run-experiment-btn {
    padding: 1.25rem 2rem;
    background: linear-gradient(135deg, var(--ai-orange), var(--ai-pink));
    border: none;
    border-radius: var(--radius-lg);
    color: var(--text-main);
    font-size: 1.05rem;
    font-weight: 700;
    cursor: pointer;
    transition: all var(--transition-base);
    box-shadow: 0 10px 30px rgba(245, 158, 11, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
  }
  
  .run-experiment-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 15px 40px rgba(245, 158, 11, 0.5);
  }
  
  .run-experiment-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  /* Output Container */
  .output-container {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
    padding: 1.5rem;
    backdrop-filter: blur(20px);
    min-height: 400px;
    max-height: 600px;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--ai-purple) transparent;
  }
  
  /* Experiment Loading */
  .experiment-loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    gap: 2rem;
  }
  
  .dna-loader {
    position: relative;
    width: 80px;
    height: 100px;
  }
  
  .dna-strand {
    position: absolute;
    width: 10px;
    height: 10px;
    background: var(--ai-purple);
    border-radius: 50%;
    animation: dnaStrand 1.5s ease-in-out infinite;
  }
  
  .dna-strand:nth-child(1) {
    left: 0;
    animation-delay: 0s;
  }
  
  .dna-strand:nth-child(2) {
    right: 0;
    animation-delay: 0.75s;
  }
  
  @keyframes dnaStrand {
    0%, 100% {
      transform: translateY(0);
      opacity: 1;
    }
    50% {
      transform: translateY(80px);
      opacity: 0.5;
    }
  }
  
  .experiment-loading p {
    color: var(--text-secondary);
    font-weight: 600;
  }
  
  /* Experiment Result */
  .experiment-result pre {
    color: rgba(255, 255, 255, 0.9);
    font-family: 'Courier New', monospace;
    font-size: 0.9rem;
    line-height: 1.7;
    white-space: pre-wrap;
    word-wrap: break-word;
    margin: 0;
  }
  
  /* Experiment Placeholder */
  .experiment-placeholder {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    gap: 1rem;
    text-align: center;
  }
  
  .experiment-placeholder span {
    font-size: 3rem;
    opacity: 0.3;
  }
  
  .experiment-placeholder p {
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
  }
  
  .experiment-error {
    color: #ef4444;
    padding: 1.5rem;
    background: rgba(239, 68, 68, 0.1);
    border: 1px solid rgba(239, 68, 68, 0.3);
    border-radius: var(--radius-md);
    text-align: center;
  }
  
  /* ===== PROJECT GENERATOR TAB ===== */
  .projects-gen {
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  .project-gen-container {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  
  /* Generation Preferences */
  .gen-preferences {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 2rem;
    backdrop-filter: blur(20px);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
  }
  
  .pref-group {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .pref-group label {
    color: var(--text-main);
    font-weight: 700;
    font-size: 0.95rem;
  }
  
  .pref-group select,
  .pref-group input {
    padding: 0.875rem 1.125rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-md);
    color: var(--text-main);
    font-size: 0.95rem;
    cursor: pointer;
    transition: all var(--transition-base);
  }
  
  .pref-group select:focus,
  .pref-group input:focus {
    outline: none;
    border-color: var(--ai-purple);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0 0 0 4px rgba(139, 92, 246, 0.1);
  }
  
  .pref-group select option {
    background: #1a1a2e;
    color: var(--text-main);
  }
  
  .difficulty-buttons {
    display: flex;
    gap: 0.5rem;
  }
  
  .diff-btn {
    flex: 1;
    padding: 0.875rem 1rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-md);
    color: var(--text-secondary);
    font-weight: 600;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all var(--transition-base);
  }
  
  .diff-btn:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: rgba(139, 92, 246, 0.5);
  }
  
  .diff-btn.active {
    background: linear-gradient(135deg, var(--ai-purple), var(--ai-pink));
    border-color: transparent;
    color: var(--text-main);
    box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
  }
  
  .generate-btn {
    grid-column: 1 / -1;
    padding: 1.25rem 2rem;
    background: linear-gradient(135deg, var(--ai-green), var(--ai-cyan));
    border: none;
    border-radius: var(--radius-lg);
    color: var(--text-main);
    font-size: 1.05rem;
    font-weight: 700;
    cursor: pointer;
    transition: all var(--transition-base);
    box-shadow: 0 10px 30px rgba(16, 185, 129, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
  }
  
  .generate-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 15px 40px rgba(16, 185, 129, 0.5);
  }
  
  .generate-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  /* Generated Projects */
  .generated-projects {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 2rem;
    animation: fadeInUp 0.6s var(--transition-base);
  }
  
  .gen-project-card {
    background: var(--glass-bg);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    padding: 2rem;
    backdrop-filter: blur(20px);
    transition: all var(--transition-base);
    animation: projectCardEntry 0.6s var(--transition-bounce);
  }
  
  @keyframes projectCardEntry {
    from {
      opacity: 0;
      transform: scale(0.9) translateY(20px);
    }
    to {
      opacity: 1;
      transform: scale(1) translateY(0);
    }
  }
  
  .gen-project-card:hover {
    transform: translateY(-8px);
    border-color: rgba(139, 92, 246, 0.5);
    box-shadow: 0 20px 60px rgba(139, 92, 246, 0.2);
  }
  
  .project-header {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .project-icon {
    font-size: 3rem;
    filter: drop-shadow(0 4px 12px rgba(139, 92, 246, 0.4));
  }
  
  .project-header h3 {
    color: var(--text-main);
    font-size: 1.35rem;
    font-weight: 800;
    margin: 0 0 0.25rem;
    line-height: 1.3;
  }
  
  .project-tagline {
    color: rgba(255, 255, 255, 0.6);
    font-size: 0.9rem;
    font-style: italic;
  }
  
  .project-description {
    color: rgba(255, 255, 255, 0.8);
    line-height: 1.7;
    margin: 0 0 1.5rem;
  }
  
  .project-meta-badges {
    display: flex;
    gap: 0.75rem;
    margin-bottom: 1.5rem;
    flex-wrap: wrap;
  }
  
  .difficulty-badge {
    padding: 0.4rem 0.9rem;
    border-radius: var(--radius-full);
    font-size: 0.75rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.5px;
  }
  
  .difficulty-badge.beginner {
    background: rgba(16, 185, 129, 0.2);
    color: var(--ai-green);
    border: 1px solid rgba(16, 185, 129, 0.5);
  }
  
  .difficulty-badge.intermediate {
    background: rgba(245, 158, 11, 0.2);
    color: var(--ai-orange);
    border: 1px solid rgba(245, 158, 11, 0.5);
  }
  
  .difficulty-badge.advanced {
    background: rgba(239, 68, 68, 0.2);
    color: #ef4444;
    border: 1px solid rgba(239, 68, 68, 0.5);
  }
  
  .timeline-badge {
    padding: 0.4rem 0.9rem;
    background: rgba(59, 130, 246, 0.2);
    color: var(--ai-blue);
    border: 1px solid rgba(59, 130, 246, 0.5);
    border-radius: var(--radius-full);
    font-size: 0.75rem;
    font-weight: 600;
  }
  
  .project-tech-stack {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
  }
  
  .tech-pill {
    padding: 0.35rem 0.85rem;
    background: rgba(139, 92, 246, 0.15);
    border: 1px solid rgba(139, 92, 246, 0.3);
    color: rgba(255, 255, 255, 0.9);
    border-radius: var(--radius-full);
    font-size: 0.8rem;
    font-weight: 600;
    transition: all var(--transition-fast);
  }
  
  .tech-pill:hover {
    background: rgba(139, 92, 246, 0.25);
    transform: scale(1.05);
  }
  
  .project-deep-dive {
    border-top: 1px solid var(--glass-border);
    padding-top: 1.5rem;
    margin-top: 1.5rem;
  }
  
  .deep-dive-item {
    margin-bottom: 1.25rem;
  }
  
  .deep-dive-item:last-child {
    margin-bottom: 0;
  }
  
  .deep-dive-item strong {
    display: block;
    color: var(--text-main);
    font-weight: 700;
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
  }
  
  .deep-dive-item p {
    color: var(--text-secondary);
    margin: 0;
    font-size: 0.9rem;
    line-height: 1.6;
  }
  
  .deep-dive-item ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .deep-dive-item li {
    color: var(--text-secondary);
    font-size: 0.85rem;
    line-height: 1.5;
    padding-left: 1.25rem;
    position: relative;
    margin-bottom: 0.5rem;
  }
  
  .deep-dive-item li::before {
    content: '';
    position: absolute;
    left: 0;
    color: var(--ai-purple);
    font-weight: 700;
  }
  
  /* Projects Placeholder */
  .projects-placeholder {
    text-align: center;
    padding: 4rem 2rem;
    animation: fadeIn 0.8s var(--transition-base);
  }
  
  .placeholder-icon {
    font-size: 5rem;
    margin-bottom: 1.5rem;
    opacity: 0.3;
    animation: float 3s ease-in-out infinite;
  }
  
  .projects-placeholder h3 {
    color: var(--text-main);
    font-size: 1.75rem;
    font-weight: 700;
    margin: 0 0 0.75rem;
  }
  
  .projects-placeholder p {
    color: rgba(255, 255, 255, 0.6);
    max-width: 600px;
    margin: 0 auto;
    line-height: 1.6;
  }
  
  /* ===== NOT LOGGED IN / LOADING STATES ===== */
  .userspace-auth-required,
  .userspace-loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    text-align: center;
    padding: 2rem;
    animation: fadeIn 0.6s var(--transition-base);
  }
  
  .auth-icon {
    font-size: 5rem;
    margin-bottom: 1.5rem;
    opacity: 0.5;
    animation: float 3s ease-in-out infinite;
  }
  
  .userspace-auth-required h2,
  .userspace-loading p {
    color: var(--text-main);
    font-size: 2rem;
    font-weight: 700;
    margin: 0 0 1rem;
  }
  
  .userspace-auth-required p {
    color: rgba(255, 255, 255, 0.6);
    margin: 0 0 2rem;
    font-size: 1.125rem;
  }
  
  .auth-features {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .feature-item {
    background: rgba(139, 92, 246, 0.1);
    border: 1px solid rgba(139, 92, 246, 0.3);
    padding: 1rem 2rem;
    border-radius: var(--radius-lg);
    color: rgba(255, 255, 255, 0.8);
    font-weight: 600;
  }
  
  .loading-spinner {
    width: 60px;
    height: 60px;
    border: 4px solid rgba(139, 92, 246, 0.2);
    border-top-color: var(--ai-purple);
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 2rem;
  }
  
  .loading-substatus {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.9rem;
    margin-top: 0.5rem;
  }
  
  /* ===== ERROR MESSAGE ===== */
  .error-message {
    background: rgba(239, 68, 68, 0.15);
    border: 1px solid rgba(239, 68, 68, 0.4);
    border-radius: var(--radius-lg);
    padding: 1.5rem;
    color: #ef4444;
    text-align: center;
    font-weight: 600;
    animation: shake 0.5s var(--transition-base);
  }
  
  @keyframes shake {
    0%, 100% { transform: translateX(0); }
    25% { transform: translateX(-10px); }
    75% { transform: translateX(10px); }
  }
  
  /* ===== RESPONSIVE DESIGN ===== */
  @media (max-width: 768px) {
    .userspace-hero {
      padding: 3rem 1rem 2rem;
    }
  
    .user-avatar-large {
      width: 100px;
      height: 100px;
    }
  
    .user-info-hero h1 {
      font-size: 2rem;
    }
  
    .userspace-tabs {
      padding: 0 1rem 1.5rem;
    }
  
    .tab-btn {
      padding: 0.875rem 1.25rem;
    }
  
    .userspace-content {
      padding: 0 1rem 2rem;
    }
  
    .section-header h2 {
      font-size: 1.5rem;
    }
  
    .brain-layout,
    .lab-experiments,
    .skills-container,
    .experiment-workspace {
      grid-template-columns: 1fr;
    }
  
    .gen-preferences {
      grid-template-columns: 1fr;
    }
  
    .generated-projects {
      grid-template-columns: 1fr;
    }
  
    .agent-grid {
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
  
    .suggestions-grid {
      grid-template-columns: 1fr;
    }
  
    .match-score-card {
      flex-direction: column;
      text-align: center;
    }
  
    .analysis-grid {
      grid-template-columns: 1fr;
    }
  
    .role-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  /* ===== ACCESSIBILITY ===== */
  @media (prefers-reduced-motion: reduce) {
    *,
    *::before,
    *::after {
      animation-duration: 0.01ms !important;
      animation-iteration-count: 1 !important;
      transition-duration: 0.01ms !important;
    }
  }
  
  /* ===== PRINT STYLES ===== */
  @media print {
    .userspace-hero,
    .userspace-tabs,
    .firestore-error-banner {
      display: none;
    }
  
    .userspace-container {
      background: white;
    }
  
    .tab-content {
      page-break-inside: avoid;
    }
  }
  
  /* ===== CUSTOM SCROLLBAR FOR FIREFOX ===== */
  * {
    scrollbar-width: thin;
    scrollbar-color: var(--ai-purple) transparent;
  }
  
  /* ===== SELECTION STYLES ===== */
  ::selection {
    background: rgba(139, 92, 246, 0.3);
    color: var(--text-main);
  }
  
  ::-moz-selection {
    background: rgba(139, 92, 246, 0.3);
    color: var(--text-main);
  }
  
  /* ===== FOCUS VISIBLE FOR ACCESSIBILITY ===== */
  button:focus-visible,
  input:focus-visible,
  textarea:focus-visible,
  select:focus-visible {
    outline: 3px solid var(--ai-purple);
    outline-offset: 2px;
  }
  
  /* ===== END OF USERSPACE.CSS ===== */


  /* --- UserSpace Professional Layout --- */

.userspace-wrapper {
    min-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;
  }
  
  .userspace-backdrop {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    padding: 80px 20px;
  }
  
  /* 1. Tech Grid Background Pattern */
  .tech-grid-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: 
      linear-gradient(rgba(255, 255, 255, 0.03) 1px, transparent 1px),
      linear-gradient(90deg, rgba(255, 255, 255, 0.03) 1px, transparent 1px);
    background-size: 40px 40px;
    mask-image: radial-gradient(circle at center, black 40%, transparent 80%);
    -webkit-mask-image: radial-gradient(circle at center, black 40%, transparent 80%);
    pointer-events: none;
    z-index: 0;
  }
  
  /* 2. The Main Access Card */
  .access-card {
    position: relative;
    width: 100%;
    max-width: 480px;
    background: rgba(20, 20, 30, 0.6); /* Darker base */
    
    backdrop-filter: blur(24px) saturate(180%);
    -webkit-backdrop-filter: blur(24px) saturate(180%);
    border-radius: 24px;
  
    border: 1px solid rgba(255, 255, 255, 0.08);
    box-shadow: 
    0 25px 50px -12px rgba(0, 0, 0, 0.15), /* Softer shadow */
    0 10px 20px -5px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.5); /* Stronger top highlight */
    padding: 40px;
    z-index: 10;
    overflow: hidden;
    transition: transform 0.3s ease;
  }
  
  /* Subtle top gradient line */
  .access-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, transparent, #a742f5, transparent);
    opacity: 0.8;
  }
  
  /* 3. Header & Icon */
  .card-header {
    text-align: center;
    margin-bottom: 35px;
  }
  
  .lock-icon-container {
    width: 60px;
    height: 60px;
    margin: 0 auto 20px;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(255, 255, 255, 0.1);
    position: relative;
  }
  
  .lock-emoji {
    font-size: 24px;
    z-index: 2;
  }
  
  /* Rotating ring around lock */
  .lock-ring {
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    border-radius: 50%;
    border: 1px dashed rgba(167, 66, 245, 0.3);
    animation: spinSlow 10s linear infinite;
  }
  
  .access-title {
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 10px;
    letter-spacing: -0.5px;
  }
  
  .access-subtitle {
    font-size: 0.95rem;
    line-height: 1.5;
    margin: 0;
  }
  
  .highlight-text {
    color: #a742f5;
    font-weight: 600;
  }
  
  /* 4. Feature Rows (Clean List) */
  .features-container {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-bottom: 35px;
  }
  
  .feature-row {
    display: flex;
    align-items: flex-start;
    gap: 16px;
    padding: 12px;
    border-radius: 12px;
    transition: background 0.2s ease;
  }
  
  .feature-row:hover {
    background: rgba(255, 255, 255, 0.03);
  }
  
  .feature-icon {
    background: rgba(167, 66, 245, 0.1);
    width: 36px;
    height: 36px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    flex-shrink: 0;
  }
  
  .feature-text {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }
  
  .f-title {
    font-size: 0.95rem;
    font-weight: 600;
  }
  
  .f-desc {
    font-size: 0.8rem;
    opacity: 0.7;
  }
  
  /* 5. Footer & Button */
  .card-footer {
    text-align: center;
    border-top: 1px solid rgba(255, 255, 255, 0.05);
    padding-top: 25px;
  }
  
  .login-wrapper {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
  }
  
  /* Force login button to look full width or prominent if needed */
  .login-wrapper button {
    min-width: 200px;
    justify-content: center;
  }
  
  .security-note {
    font-size: 0.75rem;
    color: #666; /* or a theme var */
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
  }
  
  .secure-dot {
    width: 6px;
    height: 6px;
    background-color: #10b981; /* Green for secure */
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(16, 185, 129, 0.5);
  }
  
  @keyframes spinSlow {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }
  
  /* Responsive */
  @media (max-width: 600px) {
    .access-card {
      padding: 25px;
    }
    .access-title {
      font-size: 1.5rem;
    }
  }
  /* =================================================================
