/* 1. Light Mode Variables & Global Defaults */
[data-theme="light"] {
  --bg-main: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  --text-main: #1a1a2e;       /* Dark Blue/Black for text */
  --text-secondary: #4a5568;  /* Dark Gray for secondary text */
  --bg-card: rgba(255, 255, 255, 0.75); /* White frosted glass */
  --bg-input: rgba(0, 0, 0, 0.05);      /* Light gray inputs */
  --border-light: rgba(0, 0, 0, 0.1);
  --shadow-light: 0 8px 32px 0 rgba(31, 38, 135, 0.08);
}

/* 2. Force Background & Text Colors */
[data-theme="light"].userspace-container {
  background: var(--bg-main) !important;
  color: var(--text-main) !important;
}

[data-theme="light"] .userspace-hero.enhanced {
  background: rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid var(--border-light);
}

/* 3. Flip Headings to Dark */
[data-theme="light"] h1, 
[data-theme="light"] h2, 
[data-theme="light"] h3, 
[data-theme="light"] h4, 
[data-theme="light"] h5, 
[data-theme="light"] h6,
[data-theme="light"] .user-info-hero h1,
[data-theme="light"] .section-header h2,
[data-theme="light"] .agent-name,
[data-theme="light"] .exp-name,
[data-theme="light"] .project-header h3,
[data-theme="light"] .deep-dive-item strong,
[data-theme="light"] .exp-input label, 
[data-theme="light"] .exp-output label, 
[data-theme="light"] .pref-group label {
  color: var(--text-main) !important;
  text-shadow: none !important;
}

/* 4. Flip Paragraphs & Secondary Text */
[data-theme="light"] p,
[data-theme="light"] .user-info-hero p,
[data-theme="light"] .section-header p,
[data-theme="light"] .agent-expertise,
[data-theme="light"] .exp-desc,
[data-theme="light"] .project-description,
[data-theme="light"] .project-tagline,
[data-theme="light"] .deep-dive-item p,
[data-theme="light"] .deep-dive-item li {
  color: var(--text-secondary) !important;
}

/* 5. Fix Cards (Make them white glass instead of dark glass) */
[data-theme="light"] .brain-chat,
[data-theme="light"] .brain-suggestions,
[data-theme="light"] .knowledge-graph-section,
[data-theme="light"] .agent-selector,
[data-theme="light"] .agent-task-input,
[data-theme="light"] .agent-conversation,
[data-theme="light"] .skills-input-section,
[data-theme="light"] .skills-analysis,
[data-theme="light"] .match-score-card,
[data-theme="light"] .analysis-card,
[data-theme="light"] .interview-roles,
[data-theme="light"] .interview-session,
[data-theme="light"] .experiment-selector,
[data-theme="light"] .output-container,
[data-theme="light"] .gen-preferences,
[data-theme="light"] .gen-project-card {
