.knowledge-graph-section {
  position: relative;
}

.graph-stats {
  display: flex;
  gap: 16px;
  align-items: center;
}

.node-count, .edge-count {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
}

.stat-value {
  font-weight: bold;
  font-size: 16px;
  color: #4CAF50;
}

.stat-label {
  font-size: 10px;
  color: #888;
  text-transform: uppercase;
}

.graph-canvas-wrapper {
  position: relative;
}

.knowledge-graph-canvas {
  width: 100%;
  height: 100%;
  cursor: default;
  transition: opacity 0.3s;
}

.graph-legend {
  position: absolute;
  bottom: 12px;
  right: 12px;
  background: rgba(0, 0, 0, 0.7);
  padding: 8px 12px;
  border-radius: 8px;
  display: flex;
  gap: 12px;
  font-size: 11px;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #fff;
}

.legend-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.legend-dot.profile {
  background: #4CAF50;
  box-shadow: 0 0 8px rgba(76, 175, 80, 0.6);
}

.legend-dot.project {
  background: #2196F3;
  box-shadow: 0 0 8px rgba(33, 150, 243, 0.6);
}

.legend-dot.entity {
  background: #FF9800;
  box-shadow: 0 0 8px rgba(255, 152, 0, 0.6);
}


.knowledge-graph-section {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  border-radius: 16px;
  padding: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.section-mini-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  flex-wrap: wrap;
  gap: 12px;
}

.section-mini-header h3 {
  margin: 0;
  font-size: 18px;
  color: #fff;
}

.graph-controls {
  display: flex;
  align-items: center;
  gap: 16px;
}

.node-count {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.7);
  display: flex;
  align-items: center;
  gap: 6px;
}

.count-badge {
  background: rgba(100, 200, 255, 0.2);
  color: #64d8ff;
  padding: 2px 8px;
  border-radius: 12px;
  font-weight: 600;
}

.separator {
  color: rgba(255, 255, 255, 0.3);
}

.graph-actions {
  display: flex;
  gap: 6px;
}

.graph-control-btn {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
