  justify-content: center;
  gap: 0;
}

.progress-steps .step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 0 20px;
}

.step-icon {
  width: 36px;
  height: 36px;
  border-radius: 50%;
