/* ===== PDF LOADING STATE ===== */
.pdf-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  color: rgba(255, 255, 255, 0.8);
}

.pdf-loading-animation {
  perspective: 600px;
}

.loading-page {
  width: 80px;
  height: 110px;
  background: linear-gradient(135deg, #fff 0%, #f0f0f0 100%);
  border-radius: 4px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  animation: pagePulse 1.5s ease-in-out infinite;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

@keyframes pagePulse {
  0%, 100% { 
    transform: rotateY(-10deg) scale(1); 
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  }
  50% { 
    transform: rotateY(10deg) scale(1.05); 
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
  }
}

.loading-line {
  height: 6px;
  background: linear-gradient(90deg, #e0e0e0, #c0c0c0, #e0e0e0);
  background-size: 200% 100%;
  border-radius: 3px;
  animation: shimmer 1.5s infinite;
}

.loading-line.short {
  width: 60%;
}

.loading-line.medium {
  width: 80%;
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

.loading-hint {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.4);
}

