  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 50%;
  top: 2px;
  left: 2px;
  transition: all 0.3s ease;
}

.auto-compile-toggle input:checked + .toggle-slider {
  background: linear-gradient(135deg, #4CAF50, #8BC34A);
